/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/exploring-the-flash-cs3-flvplayback-component/index.md absPath of file >>> MarkdownRemark","html":"<p>The FLVPlayback component that comes with Flash CS3 has been given a bit of a facelift, you know. (Well, the AS3 one has anyway - the older AS2 version is still the same old Flash 8 one).</p>\n<p>Both versions have support for FullScreen, High-Def video and embedded Captioning, but what 'excites' me is that the ActionScript 3 version of the FLVPlayback component has the FullScreen and Captioning features built right into the default skin and moreover, the skin is far more flexible;</p>\n<p><img src=\"http://uploads.psyked.co.uk/2008/04/blog-post-flvplayback.jpg\" alt=\"blog-post-flvplayback.jpg\"></p>\n<p>Nice, eh? You should be able to see that, in the ActionScript 3 version of the component, you are able to pick the background colour and opacity of the skin - no more messing around with custom skins for simple jobs - as well as the new component buttons in your Flash Video arsenal.</p>\n<p>Getting all excited about nothing much, you think? Well, it gets better - for fullscreen mode, in any case;</p>\n<blockquote>\n<p>Assuming that Flash Player 9 Update 3 has been applied, the prerequisites are met, and the <code>FLVPlayback.fullScreenTakeOver</code> property is set to <code>true</code>, which is the default setting, Flash Player uses hardware acceleration to scale the video file, rather than scaling it through software.</p>\n</blockquote>\n<p>If you tried watching Flash Videos in large or FullScreen modes before, you might've noticed tearing in the image - flash (and your computer / browser / flash plugin) not being able to scale the video at a fast enough rate. Lucky then, that hardware scaling support will/should solve this issue. Because, you know hardware scaling is much better than software scaling. There's practically no difference between performance when you compare the fullscreen mode to the smallscreen mode. Huzzah!</p>\n<h2>Resources / sources;</h2>\n<ul>\n<li><a href=\"http://www.adobe.com/devnet/flash/articles/flvplayback_fplayer9u3_02.html\" title=\"Open link in a new window\">Adobe : Using the FLVPlayback component with Flash Player 9 Update 3</a></li>\n</ul>","frontmatter":{"title":"Exploring the Flash CS3 FLVPlayback component","date":"April 04, 2008","tags":["flash"],"url":null}}},"pathContext":{}}

/***/ })

